/*****************************************************
Modals
*****************************************************/
.modal {
  .modal-dialog {
    margin-top: 150px;
  }
  .modal-content {
    .shadow(2px, 2px, 3px, 0, @gray3);
    .rounded(2px);
    border: 1px solid @gray4;
  }
  .modal-header {
    background: @gray2;
    .rounded(6px);
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom: 1px solid @gray3;
    padding: 12px 20px 0px;
    .close {
      font-size: 36px;
      font-weight: 400;
      margin-top: -5px;
      margin-right: -5px;
      color: @gray5;
      opacity: 1;
    }
  }
  .modal-body {
    padding: 15px 20px;
    font-size: 16px;
    min-height: 100px;
  }
  .modal-title {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.7;
  }
  .modal-footer {
    border-top-color: @gray3;
    .btn {
      padding: 6px 15px;
    }
  }
}
.modal-backdrop {
  &.in {
    opacity: 0;
    filter: alpha(opacity=0);
  }
}

// Static Patterns page display overrides
.modal {
  display: block;
  position: static;
  .modal-dialog {
    margin: 0;
  }
}
