/*****************************************************
Custom Form Grid for nesting inputs
*****************************************************/
/* Input wrapper for inline form elements */
.form-input-col, .form-input-col-1, .form-input-col-2, .form-input-col-3, .form-input-col-4,
.form-input-col-5, .form-input-col-6, .form-input-col-7, .form-input-col-8, .form-input-col-9,
.form-input-col-10, .form-input-col-11, .form-input-col-12 {
  display: inline-block;
}
.form-input-col {
  padding-right: 20px;
}
.form-input-col-3 {
  width: 25%;
}
.form-input-col-6 {
  width: 50%;
}
.form-input-col-8 {
  width: 66.66666667%;
}
.form-input-col-9 {
  width: 75%;
}
.form-input-col-12 {
  width: 100%;
}

/* Input, Select element width */
.form-control-3 {
  width: 25%;
}
.form-control-6 {
  width: 50%;
}
.form-control-9 {
  width: 75%;
}
.form-control-12 {
  width: 100%;
}
