/*****************************************************
Media Queries - Mobile First Adjustments
*****************************************************/
// Retina Logo
@media only screen
and (-webkit-min-device-pixel-ratio: 2)
and (min-resolution: '192dpi') {
  .logo {
    background-image: url('/images/zeeto-logo-horz-white-copy-4@2x.png');
  }
  .dashboard {
    background-image: url('/images/bowlcut_bg_img@2x.png');
  }
}

@media (min-width: 530px){
  .header, .navbar {
    .logo-user-signed-in {
      width: 120px;
      min-width: auto;
    }
  }
  .dashboard {
    h1 {
      padding-top: 232px;
    }
  }
}

// Desktop (bigger than tablet styles
@media (min-width: 768px){
  .container {
    padding: 0 20px 25px;
    &.full-width-components {
      padding: 0 20px;
    }
  }
  .header, .navbar {
    .logo {
      margin-left: 20px;
    }
  }
  .navbar-right .dropdown-menu {
    right: 3px;
  }
  .user-sign-up {
    background: transparent;
  }
  .breadcrumb {
    padding: 12px 20px;
  }
  .dashboard {
    h1 {
      padding-top: 218px;
      font-size: 100px;
    }
  }
  .footer {
    .footer-links {
      display: inline-block;
    }
  }
}

@media (min-width: 992px){
  .dashboard {
    background-size: initial;
  }
}

@media (min-width: 1280px){
  .alpha-col {
    width: 75%;
  }
}

