/*****************************************************
User navigation box
*****************************************************/
.user-nav {
  float: right;
  .dropdown-menu {
    width: 252px;
    left: auto;
    right: 5px;
    top: 110%;
    padding: 0;
    > li > a {
      line-height: 1.5em;
      padding: 12px 20px;
      font-size: 14px;
      font-weight: 600;
    }
    > li > a:hover, > li > a:focus {
      background-color: @dark-background;
    }
  }
}

.user-dropdown {
  display: inline-block;
  padding: 18px 20px;
  color: @white-text;
  line-height: 1.7em;
  font-size: 16px;
  font-weight: 600;
  border: none;
  vertical-align: top;
  &:hover, &:focus {
    background: rgba(116,116,116,0.5);
    color: @white-text;
    text-decoration: none;
    cursor: pointer;
    .transition(background, .5s);
  }
  .caret {
    color: @gray6-color;
    margin-left: 9px;
    /*These border calculation is to adjust the size of the caret*/
    border-top: 7px dashed;
    border-right: 7px solid transparent;
    border-left: 7px solid transparent;
  }
}
.user-sign-up {
  background: rgba(116,116,116,.5);
}
.user-nav.open .dropdown-toggle {
  box-shadow: none;
}
