/*****************************************************
Typography
*****************************************************/
// font-sizes are defined in custom bootstrap.css
h1, h2, h3, h4, h5, h6 {
  line-height: 1.6;
  font-weight: 600;
  margin: 10px 0;
}
h3 {
  line-height: 1.7;
}
h4 {
  line-height: 1.7;
}
h5, h6 {
  line-height: 1.5;
}

.paragraph-large {
  font-size: 16px;
  line-height: 1.7;
}
.paragraph-small {
  // inherits defaults
}
.empty {
  font-style: italic;
  color: @gray6-color;
}
.not-bold {
  font-weight: 500;
}

.paragraph-small-links {
  color: @link-text;
  &:hover {
    text-decoration: underline;
  }
}
.paragraph-small-links-subtle {
  color: @gray6-color;
  text-decoration: underline;
  &:hover {
    color: @gray6-color;
    text-decoration: none;
  }
}

.small-text, .small-text-semibold {
  font-size: 12px;
}
.small-text-semibold {
  font-weight: 600;
}
.section-header {
  border-bottom: 1px solid @dark-border;
}
