/*****************************************************
Navbar
*****************************************************/
// Custom styles
.header, .navbar {
  position: relative;
  min-height: 63px;
  background: @primary2;
  padding: 0;
  margin-bottom: 0;
  font-size: 16px;
  border: none;
  .rounded(0px);
  .navbar-brand {
    margin: 4px 0 7px 20px;
    padding: 10px 0;
    width: 120px;
    height: 35px;
  }
  .logo-user-signed-in {
    width: 100%;
    min-width: 380px;
  }
}
.nav.navbar-right {
  margin-right: 0;
  float: right !important;
}
.vert-bar {
  display: inline-block;
  padding: 0 0 0 10px;
}
.dropdown-group {
  display: inline-block;
}
.dropdown-menu {
  min-width: 252px;
  .shadow(2px, 2px, 3px, 0, rgba(25,25,25,.08));
}

// bootstrap overrides
.dropdown-menu > li {
  padding: 5px;
  > a {
    padding: 3px 10px;
    font-weight: 600;
  }
}

// Full Width Components Static Pattern Styles
.full-width-components {
  min-height: 0;
  padding: 0 15px;
  p {
    font-size: 16px;
    line-height: 1.7;
    font-weight: 600;
    padding: 0;
    margin: 0;
    &.hug-footer {
      margin-bottom: -25px;
    }
  }
}
