/*****************************************************
Forms
*****************************************************/
.form {
  .label {
    font-size: 14px;
    line-height: 1.5;
    font-weight: 500;
    text-align: right;
    color: @gray6;
    padding: .4em .6em .1em;
  }
}

.form-group {
  .checkbox, .radio {
    margin-top: 0;
    margin-bottom: 5px;
  }
}
::-webkit-input-placeholder {
  color: red;
}
.form-control {
  height: 33px;
  border-color: @gray4;
  color: @dark-text;
  &::-webkit-input-placeholder,
  &:-moz-placeholder ,
  &::-moz-placeholder,
  &:-ms-input-placeholder {
    color: @light-text;
  }
}
.control-label {
  width: 25%;
  display: inline-block;
  font-weight: 600;
  color: @dark-text;
  text-align: right;
  padding-top: 3px;
  margin-right: 15px;
}
.text-required {
  color: @required-red;
  font-weight: normal;
}

// Input Sizes
.lg-input {
  width: 500px;
}
.md-input {
  width: 300px;
}
.sm-input {
  width: 120px;
}

// Select Sizes
.lg-select {
  width: 300px;
}
.md-select {
  width: 200px;
}
.sm-select {
  width: 120px;
}

// Sign In Form
.sign-in {
  .label {
    color: @gray6;
    font-size: 18px;
    font-weight: 500;
    padding-left: 0;
    line-height: 1.7;
  }
  .form-control {
    height: 43px;
    font-size: 16px;
    line-height: 1.7;
    padding: 9px 18px;
  }
  .form-group-error {
    .form-control {
      border-color: @required-red;
      color: @required-red;
    }
    .form-error {
      color: @required-red;
    }
  }
  .form-error {
    font-size: 16px;
  }
}

// Error classes
.form-group-error {
  .form-control {
    border-color: @red;
    color: @red;
  }
  .form-error {
    color: @red;
    margin-top: 3px;
  }
}
