/*****************************************************
Mixins
*****************************************************/
.transition(@element, @timing){
  -webkit-transition: @element @timing;
  -moz-transition: @element @timing;
  transition: @element @timing;
}
.rounded(@size){
  -webkit-border-radius: @size;
  -moz-border-radius: @size;
  border-radius: @size;
}
.shadow(@horiz, @vert, @spread, @blur, @color){
  -webkit-box-shadow: @horiz @vert @spread @blur @color;
  -moz-box-shadow: @horiz @vert @spread @blur @color;
  box-shadow: @horiz @vert @spread @blur @color;
}
