/*****************************************************
Alerts
*****************************************************/
.alert {
  margin-top: 10px;
  padding: 15px 20px;
  font-size: 16px;
  line-height: 1.7;
  strong {
    font-weight: 600;
  }
  .rounded(4px);
}
.error-alert, .success-alert, .info-alert, .warning-alert {
  color: @gray9;
  border: none;
}
.error-alert {
  background: @lightred-color;
}
.info-alert {
  background: @lightblue-color;
}
.success-alert {
  background: @lightgreen-color;
}

// Static Patterns page display styles
.user-feedback {
  p {
    font-size: 16px;
    margin: 0 0 -8px;
  }
}
