/*****************************************************
Buttons
*****************************************************/
.button-bottom-margin {
  margin-bottom: 30px;
}
.primary-button,
.action-button,
.utility-button {
  color: @white;
  &:hover, &:focus {
    color: @white;
  }
}
.primary-button {
  background-color: @blue-color;
}
.action-button {
  background-color: @green-color;
}
.utility-button {
  background-color: @gray9-color;
}
.control-button {
  background: @gray3;
}
.disabled-button {
  color: @gray4;
  background: @gray2;
  &:hover, &:focus {
    color: @gray4;
  }
}
// bootstrap button size overrides
.btn {
  font-weight: 600;
  border-color: transparent;
  padding: 5px 12px;
  &:hover {
    border-color: transparent;
  }
  &.expand {
    width: 100%;
  }
  &.not-bold {
    font-weight: 500;
  }
}
.sm-size {
  font-size: 12px;
  padding: 4px 12px 5px;
}
.md-size {
  padding: 6px 15px;
}
.lg-size {
  font-size: 16px;
  line-height: 1.7;
  padding: 6px 20px;
}
.xl-size {
  font-size: 18px;
  padding: 7px 25px 8px;
}

.btn-inverse {
  background-color: @primary2-color;
  color: @white-text;
  &:hover, &:focus {
    background-color: darken(@primary2-color, 10%);
    color: @white-text;
  }
}
.btn-default {
  background-color: @gray3;
  &:hover {
    background-color: darken(@gray3, 10%);
  }
}
