/*****************************************************
Breadcrumbs
*****************************************************/
.breadcrumb {
  margin-bottom: 18px;
  padding: 12px 15px;
  font-size: 14px;
  border-bottom: 1px solid @dark-border;
  background: @gray2;
  li {
    color: @light-text;
    display: inline-block;
    a {
      line-height: 1.5em;
    }
  }
}
