@white:               #ffffff;
@white-color:         @white;

@black:               #000000;
@black-color:         @black;
@black-text:          @black;

@primary:             #ff751a;
@primary2:            #575757;

@red:                 #e03c29;
@required-red:        #e6333e;
@green:               #29a64b;
@blue:                #2c76e1;
@link-color:          #1e48d9;

@gray9:               #3a3a3a;
@gray8:               @primary2;
@gray7:               #747474;
@gray6:               #969696;
@gray5:               #b9b9b9;
@gray4:               #d1d1d1;
@gray3:               #e8e8e8;
@gray2:               #f3f3f3;
@gray1:               #f9f9f9;

@gray9-color:         @gray9;
@gray8-color:         @gray8;
@gray7-color:         @gray7;
@gray6-color:         @gray6;
@gray5-color:         @gray5;
@gray4-color:         @gray4;
@gray3-color:         @gray3;
@gray2-color:         @gray2;
@gray1-color:         @gray1;

@primary1-color:      @primary;
@primary2-color:      @primary2;

@blue-color:          @blue;
@green-color:         @green;
@red-color:           @red;
@lightblue-color:     #edf1fc;
@lightgreen-color:    #eef8f1;
@lightred-color:      #fdf0ee;

@dark-border:         @gray4;
@light-border:        @gray3;

@dark-background:     @gray2;
@light-background:    @gray1;
@white-background:    @white;

@dark-text:           @gray9;
@light-text:          @gray6;
@white-text:          @white;
@disabled-text:       @gray4;
@primary1-text:       @primary;
@link-text:           @link-color;
@success-text:        @green;
@error-text:          @red;

// Usage
.color-bottom-margin {
  margin-bottom: 15px;
}
// Colors for Static Patterns Page
.color-block {
  text-align: center;
  font-size: 16px;
  padding: 10px;
  margin: 4px 0;
  border: 1px solid transparent;
  &.pattern-primary1-color {
    background: @primary1-color;
  }
  &.pattern-primary2-color {
    background: @primary2-color;
  }
  &.pattern-blue-color {
    background: @blue-color;
  }
  &.pattern-green-color {
    background: @green-color;
  }
  &.pattern-red-color {
    background: @red-color;
  }
  &.pattern-lightblue-color {
    background: @lightblue-color;
  }
  &.pattern-lightgreen-color {
    background: @lightgreen-color;
  }
  &.pattern-lightred-color {
    background: @lightred-color;
  }
  &.pattern-gray9-color {
    background: @gray9-color;
  }
  &.pattern-gray8-color {
    background: @gray8-color;
  }
  &.pattern-gray7-color {
    background: @gray7-color;
  }
  &.pattern-gray6-color {
    background: @gray6-color;
  }
  &.pattern-gray5-color {
    background: @gray5-color;
  }
  &.pattern-gray4-color {
    background: @gray4-color;
  }
  &.pattern-gray3-color {
    background: @gray3-color;
  }
  &.pattern-gray2-color {
    background: @gray2-color;
  }
  &.pattern-gray1-color {
    background: @gray1-color;
  }
  &.pattern-white-color {
    background: @white-color;
    border-color: @gray3;
  }

  &.pattern-dark-border {
    background: @gray4-color;
  }
  &.pattern-light-border {
    background: @gray3-color;
  }

  &.pattern-dark-background {
    background: @dark-background;
  }
  &.pattern-light-background {
    background: @light-background;
  }
  &.pattern-white-background {
    background: @white-background;
    border-color: @gray3;
  }

  &.pattern-dark-text {
    background: @dark-text;
  }
  &.pattern-light-text {
    background: @light-text;
  }
  &.pattern-white-text {
    background: @white-text;
    border-color: @gray3;
  }
  &.pattern-disabled-text {
    background: @disabled-text;
  }
  &.pattern-primary1-text {
    background: @primary1-text;
  }
  &.pattern-link-text {
   background: @link-text;
  }
  &.pattern-success-text {
    background: @success-text;
  }
  &.pattern-error-text {
    background: @error-text;
  }
}

.color-block-dark {
  color: @white;
}

