/*****************************************************
Sign In Form
*****************************************************/
.sign-in-container {
  width: 100%;
  max-width: 450px;
  margin: auto;
  h1 {
    margin: 29px auto 12px;
    text-align: center;
  }
  .form-group {
    margin-bottom: 12px;
  }
  .label {
    padding-left: 0;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.7;
    color: @gray6;
  }
  .form-control {
    height: 45px;
    font-size: 16px;
    line-height: 1.7;
    color: @gray9;
  }
  .btn-expand {
    width: 100%;
    height: 45px;
    margin-top: 10px;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.7;
  }

  .new-user-links, .forgot-password {
    text-align: center;
    margin: 20px auto;
    font-size: 16px;
    line-height: 1.7;
  }
  .forgot-password {
    margin-top: 28px;
    margin-bottom: 47px;
  }
}