/*****************************************************
Custom Table Styles
*****************************************************/
// table overrides
.table > thead > tr > th,
.table > tbody > tr > th,
.table > tfoot > tr > th,
.table > thead > tr > td,
.table > tbody > tr > td,
.table > tfoot > tr > td {
  vertical-align: middle;
  border-top: 0px;
}
.table > thead > tr > th,
.table > tbody > tr > th,
.table > tfoot > tr > th {
  height: 42px;
  padding: 10px 15px;
  font-weight: 600;
  border-color: transparent;
  background: rgba(232, 232, 232, .5);
}
.table > thead > tr > td,
.table > tbody > tr > td,
.table > tfoot > tr > td {
  height: 44px;
  padding: 8px 15px;
}

.table {
  thead {
    border-bottom: 3px solid @white;
    tr {
      border-bottom: none;
    }
    .th-right-border {
      border-right: 2px solid @white;
    }
    th.accounting-id {
      width: 200px;
      min-width: 200px;
    }
    th.action {
      width: 130px;
      min-width: 130px;
    }
    th.advertising-accounts {
      width: 250px;
    }
  }
  tbody {
    tr {
      border-bottom: 1px solid @gray3;
      &:hover {
        background-color: rgba(243, 243, 243, .5);
      }
    }
    .no-hover {
      &:hover {
        background: none;
      }
    }
  }
}
