/*****************************************************
Control Bar
*****************************************************/
.table-control-bar {
  margin: 0 0 21px;
  .btn-sm {
    background-color: rgba(209, 209, 209, .5);
    font-size: 14px;
    &:hover {
      background-color: rgba(209, 209, 209, .7);
    }
  }
  .select-group-btn {
    background: rgba(209, 209, 209, .5);
    padding: 6px 15px;
    border-radius: 2px;
    font-weight: 600;
    &:hover {
      background-color: rgba(209, 209, 209, .7);
      cursor: pointer;
    }
    &.open {
      background-color: rgba(209, 209, 209, .7);
    }
    a {
      color: @gray9;
    }
    .caret {
      margin-left: 9px;
      position: relative;
      top: -2px;
    }
  }
  .dropdown-menu {
    width: 202px;
    top: 110%;
    border: 1px solid @gray4;
    padding: 8px 0;
    > li > a {
      line-height: 1.5em;
      padding: 9px 12px;
      font-size: 14px;
      font-weight: 600;
      .transition(background, .5s);
      &:hover {
        background-color: rgba(243, 243, 243, 1);
      }
      &:active, &.active {
        background-color: rgba(209, 209, 209, 1);
      }
    }
  }
}
